@import url(https://fonts.googleapis.com/css2?family=DM+Mono:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {

}

.nav-menu{box-shadow:3px 3px 15px 0px rgba(0,0,0,0.2);border:5px solid black;padding:20px;margin:20px}.nav-menu .header h1{margin:10px;font-size:3em}.nav-menu .mobile{display:none}.nav-menu .nav-items a{min-height:2em;color:inherit;text-decoration:none;cursor:pointer;padding:10px;font-weight:bold;display:flex;align-items:center}.nav-menu .nav-items a.active{background:black;color:white}.nav-menu .nav-items a .index{font-weight:normal;font-size:2em;margin-right:10px}.nav-menu .nav-items a .title span{display:block}.nav-menu .nav-items a .title span:last-child{font-size:0.8em}.nav-menu button{background:none;border:none;outline:none;padding:0 10px 0 0}.nav-menu button img{height:20px;width:20px}@media (max-width: 400px){.nav-menu .mobile{display:block}.nav-menu .header{display:flex;align-items:center}.nav-menu .header h1{font-size:2em;margin:10px 5px}.nav-menu .nav-items{box-shadow:3px 0px 15px 0px rgba(0,0,0,0.2);position:fixed;top:0;left:-100%;background:white;height:100%;z-index:1;transition:all 400ms cubic-bezier(0.39, 0.8, 0.43, 0.98)}.nav-menu .nav-items.open{left:0}}

.step-wrapper{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh}.step-wrapper .step{box-shadow:0px 10px 0px 0px #6c6c6c;position:relative;min-height:40vh;min-width:40vh;background:#FFFFFF;border-radius:15px;padding:30px;display:flex;justify-content:center;align-items:center;flex-direction:column;transition:all 100ms ease-in 0s;cursor:pointer}.step-wrapper .step:not(.active):active{transform:translateY(10px);box-shadow:0px 0px 0px 0px #cc9d0e}.step-wrapper .step.active{box-shadow:0px 10px 0px 0px #cc9d0e;background:#ffca25}.step-wrapper .step .instruction{position:absolute;bottom:20px;left:0;width:100%;text-align:center;font-size:1.5em}.step-wrapper .step h1{margin:40px 0;position:absolute;top:0;left:0;width:100%;text-align:center}.step-wrapper .step .timer{font-size:3.5em}.step-wrapper .arrows{color:white;margin:5vh 0;display:flex;justify-content:center}.step-wrapper .arrows .arrow{margin:0 20px}

.routes .page-enter{opacity:0;transform:translate(0, 30px)}.routes .page-enter-active{opacity:1;transform:translate(0, 0);transition:all 300ms, transform 300ms}.routes .page-exit{opacity:1;transform:translate(0, 0)}.routes .page-exit-active{opacity:0;transform:translate(0, 30px);transition:all 300ms, transform 300ms}

.App{font-family:'DM Mono', monospace;background:linear-gradient(180deg, #2b2b2b 0%, #000 50%)}

